@import "https://fonts.googleapis.com/css2?family=Comic+Neue:wght@400;700&family=Nunito:wght@400;600;700&family=Bubblegum+Sans&family=Caveat:wght@400;700&family=Ma+Shan+Zheng&display=swap";
@import "https://fonts.googleapis.com/css2?family=ZCOOL+QingKe+HuangYou&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #fef5f8;
  background-image: linear-gradient(120deg, #fef5f8 0%, #fde2e7 100%);
  font-family: Bubblegum Sans, ZCOOL QingKe HuangYou, Comic Neue, Nunito, Microsoft YaHei, Arial Rounded MT Bold, Arial, sans-serif;
  line-height: 1.6;
  transition: all .3s;
  overflow-x: hidden;
}

body.pink {
  background-color: #fef5f8;
  background-image: linear-gradient(120deg, #fef5f8 0%, #fde2e7 100%);
}

body.white {
  background-color: #fff;
  background-image: linear-gradient(120deg, #fff 0%, #f8f9fa 100%);
}

body.blue {
  background-color: #f0f9ff;
  background-image: linear-gradient(120deg, #f0f9ff 0%, #e0f2fe 100%);
}

.container {
  backdrop-filter: blur(12px);
  background-color: #fffffff2;
  border-radius: 28px;
  max-width: 1000px;
  margin: 30px auto;
  padding: 40px;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 32px #0000001a, 0 1px 3px #00000014, inset 0 1px #ffffff80;
}

.container:before {
  content: "";
  z-index: 1;
  background: linear-gradient(90deg, #0000, #fffc, #0000);
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

header {
  text-align: center;
  margin-bottom: 50px;
  padding-bottom: 30px;
  position: relative;
}

header:after {
  content: "";
  background: linear-gradient(90deg, #0000, #ff6b9d, #0000);
  border-radius: 2px;
  width: 100px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

header h1 {
  letter-spacing: 1.2px;
  margin-bottom: 25px;
  font-size: 40px;
  font-weight: bold;
  animation: .5s titleFadeIn;
  display: inline-block;
  position: relative;
}

body.pink header {
  border-bottom: 1px solid #ff6b9d33;
}

body.pink header h1 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #ff6b9d1a;
  background: linear-gradient(120deg, #ff6b9d, #ff85b0) text;
}

body.pink header:after {
  background: linear-gradient(90deg, #0000, #ff6b9d, #0000);
}

body.white header {
  border-bottom: 1px solid #0000001a;
}

body.white header h1 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #0000001a;
  background: linear-gradient(120deg, #333, #666) text;
}

body.white header:after {
  background: linear-gradient(90deg, #0000, #333, #0000);
}

body.blue header {
  border-bottom: 1px solid #4a90e233;
}

body.blue header h1 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #4a90e21a;
  background: linear-gradient(120deg, #4a90e2, #6aa9f4) text;
}

body.blue header:after {
  background: linear-gradient(90deg, #0000, #4a90e2, #0000);
}

@keyframes titleFadeIn {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

main {
  flex-direction: column;
  gap: 50px;
  display: flex;
}

section {
  background-color: #fffffff2;
  border-radius: 24px;
  padding: 40px;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 16px #0000001a, 0 1px 3px #00000014, inset 0 1px #ffffff80;
}

section:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fffc, #0000);
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

section h2 {
  text-align: center;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  padding-bottom: 20px;
  font-size: 32px;
  font-weight: bold;
  display: flex;
  position: relative;
}

section h2:after {
  content: "";
  background: linear-gradient(90deg, #0000, #ff6b9d, #0000);
  border-radius: 2px;
  width: 80px;
  height: 3px;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

section:hover h2:after {
  width: 120px;
}

body.pink section {
  border: 2px solid #ff6b9d4d;
}

body.pink section:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 24px #ff6b9d33, 0 2px 6px #ff6b9d1a, inset 0 1px #fffc;
}

body.pink section h2 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #ff6b9d1a;
  background: linear-gradient(120deg, #ff6b9d, #ff85b0) text;
}

body.pink section h2:after {
  background: linear-gradient(90deg, #0000, #ff6b9d, #0000);
}

body.white section {
  border: 2px solid #0003;
}

body.white section:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 24px #0003, 0 2px 6px #0000001a, inset 0 1px #fffc;
}

body.white section h2 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #0000001a;
  background: linear-gradient(120deg, #333, #666) text;
}

body.white section h2:after {
  background: linear-gradient(90deg, #0000, #333, #0000);
}

body.blue section {
  border: 2px solid #4a90e24d;
}

body.blue section:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 24px #4a90e233, 0 2px 6px #4a90e21a, inset 0 1px #fffc;
}

body.blue section h2 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #4a90e21a;
  background: linear-gradient(120deg, #4a90e2, #6aa9f4) text;
}

body.blue section h2:after {
  background: linear-gradient(90deg, #0000, #4a90e2, #0000);
}

section h2:before {
  content: "üéÄ";
  flex-shrink: 0;
  font-size: 28px;
}

.gift-grid {
  backdrop-filter: blur(8px);
  background-color: #ffffffb3;
  border-radius: 24px;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 40px;
  padding: 30px;
  transition: all .3s;
  display: grid;
  box-shadow: inset 0 1px #ffffff80, 0 2px 8px #00000014;
}

body.pink .gift-grid {
  background-color: #fff5f8b3;
  border: 3px dashed #ff6b9d4d;
}

body.white .gift-grid {
  background-color: #ffffffb3;
  border: 3px dashed #0003;
}

body.blue .gift-grid {
  background-color: #f0f9ffb3;
  border: 3px dashed #4a90e24d;
}

.gift-item {
  background-color: #fffffff2;
  border-radius: 20px;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 16px #0000001a, 0 1px 3px #00000014, inset 0 1px #ffffff80;
}

.gift-item:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fffc, #0000);
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

body.pink .gift-item {
  border: 2px solid #ff6b9d4d;
}

body.pink .gift-item:hover {
  border-color: #ff6b9d;
  transform: translateY(-12px)rotate(1deg);
  box-shadow: 0 8px 24px #ff6b9d33, 0 2px 6px #ff6b9d1a, inset 0 1px #fffc;
}

body.white .gift-item {
  border: 2px solid #0003;
}

body.white .gift-item:hover {
  border-color: #333;
  transform: translateY(-12px)rotate(1deg);
  box-shadow: 0 8px 24px #0003, 0 2px 6px #0000001a, inset 0 1px #fffc;
}

body.blue .gift-item {
  border: 2px solid #4a90e24d;
}

body.blue .gift-item:hover {
  border-color: #4a90e2;
  transform: translateY(-12px)rotate(1deg);
  box-shadow: 0 8px 24px #4a90e233, 0 2px 6px #4a90e21a, inset 0 1px #fffc;
}

.gift-media-container {
  width: 100%;
  height: 180px;
  position: relative;
  overflow: hidden;
}

.gift-media {
  object-fit: cover;
  cursor: pointer;
  width: 100%;
  height: 100%;
  transition: transform .3s;
}

.gift-item:hover .gift-media {
  transform: scale(1.05);
}

.gift-note {
  text-align: center;
  white-space: pre-wrap;
  word-break: break-word;
  background-color: #fefeff;
  min-height: 90px;
  padding: 18px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.6;
  transition: all .3s;
}

body.pink .gift-note {
  background-color: #fef5f8;
  border-top: 2px solid #ffd6e4;
}

body.pink .gift-item:hover .gift-note {
  background-color: #fde2e7;
}

body.white .gift-note {
  background-color: #f8f9fa;
  border-top: 2px solid #ddd;
}

body.white .gift-item:hover .gift-note {
  background-color: #fff;
}

body.blue .gift-note {
  background-color: #f0f9ff;
  border-top: 2px solid #b3d1f3;
}

body.blue .gift-item:hover .gift-note {
  background-color: #e0f2fe;
}

#letter-title {
  text-align: center;
  border-radius: 20px;
  margin-bottom: 25px;
  padding: 15px;
  font-size: 24px;
  font-weight: bold;
}

.letter-content {
  white-space: pre-wrap;
  background-color: #f9f6f0;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns=\"http://www.w3.org/2000/svg\" width=\"100\" height=\"100\" viewBox=\"0 0 100 100\"><rect width=\"100\" height=\"100\" fill=\"%23f9f6f0\"/><path d=\"M0 10 L100 10 M0 20 L100 20 M0 30 L100 30 M0 40 L100 40 M0 50 L100 50 M0 60 L100 60 M0 70 L100 70 M0 80 L100 80 M0 90 L100 90\" stroke=\"%23e0d6c2\" stroke-width=\"0.5\" stroke-opacity=\"0.3\"/></svg>");
  border-radius: 20px;
  min-height: 400px;
  padding: 40px;
  font-family: Bubblegum Sans, ZCOOL QingKe HuangYou, Comic Neue, Nunito, Microsoft YaHei, sans-serif;
  font-size: 20px;
  line-height: 2;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 16px #0000001a;
}

body.pink .letter-content {
  background-color: #fef5f8;
  background-image: linear-gradient(120deg, #ff6b9d0d 0%, #fffffff2 100%);
}

body.white .letter-content {
  background-color: #f8f9fa;
  background-image: linear-gradient(120deg, #00000008 0%, #fffffff7 100%);
}

body.blue .letter-content {
  background-color: #f0f9ff;
  background-image: linear-gradient(120deg, #4a90e20d 0%, #fffffff2 100%);
}

body.pink .letter-content:hover {
  border-color: #ff6b9d;
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #ff6b9d33;
}

body.white .letter-content:hover {
  border-color: #333;
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #0003;
}

body.blue .letter-content:hover {
  border-color: #4a90e2;
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #4a90e233;
}

@keyframes emojiFall {
  0% {
    opacity: 1;
    transform: translateY(-30px)rotate(0);
  }

  100% {
    opacity: 0;
    transform: translateY(500px)rotate(360deg);
  }
}

.emoji-rain {
  pointer-events: none;
  z-index: 10;
  animation-timing-function: ease-in-out;
  position: absolute;
}

.gift-item img, .gift-item video {
  object-fit: cover;
  cursor: pointer;
  width: 100%;
  height: 180px;
  transition: transform .3s;
}

.gift-item:hover img, .gift-item:hover video {
  transform: scale(1.05);
}

#save-status {
  color: #fff;
  z-index: 1000;
  opacity: 0;
  border-radius: 5px;
  padding: 12px 20px;
  transition: all .3s;
  position: fixed;
  top: 20px;
  right: 20px;
  transform: translateY(-20px);
  box-shadow: 0 2px 10px #0003;
}

body.pink #save-status {
  background-color: #ff6b9d;
  box-shadow: 0 2px 10px #ff6b9d66;
}

body.white #save-status {
  background-color: #333;
  box-shadow: 0 2px 10px #0006;
}

body.blue #save-status {
  background-color: #4a90e2;
  box-shadow: 0 2px 10px #4a90e266;
}

body.pink #letter-title {
  color: #ff6b9d;
  text-shadow: 1px 1px 2px #ff6b9d33;
  background-color: #fef5f8;
  border: 2px solid #ffd6e4;
}

body.pink .letter-content {
  border: 3px solid #ffd6e4;
}

body.pink .letter-content:hover {
  border-color: #ff6b9d;
  box-shadow: 0 8px 24px #ff6b9d33;
}

body.white #letter-title {
  color: #333;
  text-shadow: 1px 1px 2px #0000001a;
  background-color: #f8f9fa;
  border: 2px solid #ddd;
}

body.white .letter-content {
  border: 3px solid #ddd;
}

body.white .letter-content:hover {
  border-color: #333;
  box-shadow: 0 8px 24px #0003;
}

body.blue #letter-title {
  color: #4a90e2;
  text-shadow: 1px 1px 2px #4a90e233;
  background-color: #f0f9ff;
  border: 2px solid #b3d1f3;
}

body.blue .letter-content {
  border: 3px solid #b3d1f3;
}

body.blue .letter-content:hover {
  border-color: #4a90e2;
  box-shadow: 0 8px 24px #4a90e233;
}

.music-section {
  justify-content: center;
  margin: 30px 0;
  display: flex;
}

.music-player {
  border-radius: 30px;
  align-items: center;
  gap: 20px;
  padding: 20px 40px;
  transition: all .3s;
  display: flex;
  box-shadow: 0 4px 16px #0000001a;
}

.music-name {
  color: #666;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex-shrink: 0;
  max-width: 200px;
  margin-left: 10px;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
  overflow: hidden;
}

body.pink .music-name {
  color: #ff6b9d;
}

body.white .music-name {
  color: #333;
}

body.blue .music-name {
  color: #4a90e2;
}

#play-btn {
  cursor: pointer;
  background-color: #fff;
  border-radius: 25px;
  padding: 12px 30px;
  font-weight: bold;
  transition: all .3s;
}

#volume {
  -webkit-appearance: none;
  border-radius: 4px;
  outline: none;
  width: 120px;
  height: 8px;
  transition: all .3s;
}

#volume::-webkit-slider-thumb {
  appearance: none;
  cursor: pointer;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: all .3s;
}

#volume::-webkit-slider-thumb:hover {
  transform: scale(1.2);
}

body.pink .music-player {
  background-color: #fef5f8;
  border: 2px solid #ffd6e4;
}

body.pink .music-player:hover {
  background-color: #fde2e7;
  border-color: #ff6b9d;
  box-shadow: 0 8px 24px #ff6b9d33;
}

body.pink #play-btn {
  color: #ff6b9d;
  border: 2px solid #ff6b9d;
}

body.pink #play-btn:hover {
  color: #fff;
  background-color: #ff6b9d;
  transform: scale(1.05);
}

body.pink #volume {
  background: #ffd6e4;
}

body.pink #volume:hover {
  background: #ff6b9d;
}

body.pink #volume::-webkit-slider-thumb {
  background: #ff6b9d;
}

body.pink #volume::-webkit-slider-thumb:hover {
  background: #ff528b;
}

body.white .music-player {
  background-color: #f8f9fa;
  border: 2px solid #ddd;
}

body.white .music-player:hover {
  background-color: #fff;
  border-color: #333;
  box-shadow: 0 8px 24px #0003;
}

body.white #play-btn {
  color: #333;
  border: 2px solid #333;
}

body.white #play-btn:hover {
  color: #fff;
  background-color: #333;
  transform: scale(1.05);
}

body.white #volume {
  background: #ddd;
}

body.white #volume:hover {
  background: #333;
}

body.white #volume::-webkit-slider-thumb {
  background: #333;
}

body.white #volume::-webkit-slider-thumb:hover {
  background: #555;
}

body.blue .music-player {
  background-color: #f0f9ff;
  border: 2px solid #b3d1f3;
}

body.blue .music-player:hover {
  background-color: #e0f2fe;
  border-color: #4a90e2;
  box-shadow: 0 8px 24px #4a90e233;
}

body.blue #play-btn {
  color: #4a90e2;
  border: 2px solid #4a90e2;
}

body.blue #play-btn:hover {
  color: #fff;
  background-color: #4a90e2;
  transform: scale(1.05);
}

body.blue #volume {
  background: #b3d1f3;
}

body.blue #volume:hover {
  background: #4a90e2;
}

body.blue #volume::-webkit-slider-thumb {
  background: #4a90e2;
}

body.blue #volume::-webkit-slider-thumb:hover {
  background: #357abd;
}

footer {
  text-align: center;
  margin-top: 60px;
  padding-top: 35px;
}

.save-tip {
  border-radius: 25px;
  padding: 20px 30px;
  font-size: 18px;
  font-weight: bold;
  transition: all .3s;
  animation: 2s infinite pulse;
  display: inline-block;
}

body.pink footer {
  border-top: 3px solid #ffd6e4;
}

body.pink .save-tip {
  color: #ff6b9d;
  background-color: #ff6b9d1a;
  border: 2px solid #ffd6e4;
  box-shadow: 0 4px 16px #ff6b9d1a;
}

body.pink .save-tip:hover {
  background-color: #ff6b9d33;
  border-color: #ff6b9d;
  transform: scale(1.05);
  box-shadow: 0 8px 24px #ff6b9d33;
}

body.white footer {
  border-top: 3px solid #ddd;
}

body.white .save-tip {
  color: #333;
  background-color: #0000001a;
  border: 2px solid #ddd;
  box-shadow: 0 4px 16px #0000001a;
}

body.white .save-tip:hover {
  background-color: #0003;
  border-color: #333;
  transform: scale(1.05);
  box-shadow: 0 8px 24px #0003;
}

body.blue footer {
  border-top: 3px solid #b3d1f3;
}

body.blue .save-tip {
  color: #4a90e2;
  background-color: #4a90e21a;
  border: 2px solid #b3d1f3;
  box-shadow: 0 4px 16px #4a90e21a;
}

body.blue .save-tip:hover {
  background-color: #4a90e233;
  border-color: #4a90e2;
  transform: scale(1.05);
  box-shadow: 0 8px 24px #4a90e233;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 4px 16px #ff6b9d1a;
  }

  50% {
    transform: scale(1.05);
    box-shadow: 0 8px 24px #ff6b9d33;
  }

  100% {
    transform: scale(1);
    box-shadow: 0 4px 16px #ff6b9d1a;
  }
}

.save-tip:hover {
  background-color: #ff6b9d33;
  border-color: #ff6b9d;
  transform: scale(1.05);
  box-shadow: 0 8px 24px #ff6b9d33;
}

.modal {
  z-index: 1000;
  backdrop-filter: blur(10px);
  background-color: #000000b3;
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  text-align: center;
  background-color: #fff;
  border-radius: 25px;
  width: 90%;
  max-width: 850px;
  margin: 5% auto;
  padding: 30px;
  animation: .3s modalFadeIn;
  position: relative;
}

body.pink .modal-content {
  background-image: linear-gradient(120deg, #fff 0%, #fef5f8 100%);
  border: 3px solid #ff6b9d;
  box-shadow: 0 10px 40px #ff6b9d66;
}

body.white .modal-content {
  background-image: linear-gradient(120deg, #fff 0%, #f8f9fa 100%);
  border: 3px solid #333;
  box-shadow: 0 10px 40px #0006;
}

body.blue .modal-content {
  background-image: linear-gradient(120deg, #fff 0%, #f0f9ff 100%);
  border: 3px solid #4a90e2;
  box-shadow: 0 10px 40px #4a90e266;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: scale(.9)translateY(-50px);
  }

  to {
    opacity: 1;
    transform: scale(1)translateY(0);
  }
}

.close {
  float: right;
  cursor: pointer;
  z-index: 10;
  font-size: 36px;
  font-weight: bold;
  transition: all .3s;
  position: absolute;
  top: 15px;
  right: 25px;
}

.close:hover, .close:focus {
  cursor: pointer;
  text-decoration: none;
  transform: scale(1.1)rotate(90deg);
}

body.pink .close {
  color: #ff6b9d;
}

body.pink .close:hover, body.pink .close:focus {
  color: #ff528b;
}

body.white .close {
  color: #333;
}

body.white .close:hover, body.white .close:focus {
  color: #555;
}

body.blue .close {
  color: #4a90e2;
}

