    font-size: 20px;
    line-height: 2;
    white-space: pre-wrap;
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 100 100"><rect width="100" height="100" fill="%23f9f6f0"/><path d="M0 10 L100 10 M0 20 L100 20 M0 30 L100 30 M0 40 L100 40 M0 50 L100 50 M0 60 L100 60 M0 70 L100 70 M0 80 L100 80 M0 90 L100 90" stroke="%23e0d6c2" stroke-width="0.5" stroke-opacity="0.3"/></svg>');
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

/* ‰∏ªÈ¢òËâ≤‰π¶‰ø°ÂÜÖÂÆπÊ†∑Âºè */
