/* ÂºπÁ™óÊ†∑Âºè */
.modal {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
}

.modal-content {
    background-color: #fff;
    margin: 5% auto;
    padding: 30px;
    width: 90%;
    max-width: 850px;
    border-radius: 25px;
    position: relative;
    text-align: center;
    animation: modalFadeIn 0.3s ease;
}

/* ‰∏ªÈ¢òËâ≤ÂºπÁ™óÊ†∑Âºè */
body.pink .modal-content {
    border: 3px solid #ff6b9d;
    box-shadow: 0 10px 40px rgba(255, 107, 157, 0.4);
    background-image: linear-gradient(120deg, #fff 0%, #fef5f8 100%);
}

body.white .modal-content {
    border: 3px solid #333;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(120deg, #fff 0%, #f8f9fa 100%);
}

body.blue .modal-content {
    border: 3px solid #4a90e2;
    box-shadow: 0 10px 40px rgba(74, 144, 226, 0.4);
    background-image: linear-gradient(120deg, #fff 0%, #f0f9ff 100%);
}

@keyframes modalFadeIn {
    from {
        opacity: 0;
        transform: scale(0.9) translateY(-50px);
    }
    to {
        opacity: 1;
        transform: scale(1) translateY(0);
    }
}

.close {
    float: right;
    font-size: 36px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
    position: absolute;
    top: 15px;
    right: 25px;
    z-index: 10;
}

.close:hover,
.close:focus {
    text-decoration: none;
    cursor: pointer;
    transform: scale(1.1) rotate(90deg);
}

/* ‰∏ªÈ¢òËâ≤ÂÖ≥Èó≠ÊåâÈíÆÊ†∑Âºè */
body.pink .close {
    color: #ff6b9d;
}

body.pink .close:hover,
body.pink .close:focus {
    color: #ff528b;
}

body.white .close {
    color: #333;
}

body.white .close:hover,
body.white .close:focus {
    color: #555;
}

body.blue .close {
    color: #4a90e2;
}

body.blue .close:hover,
body.blue .close:focus {
    color: #357abd;
}

#modal-image {
    max-width: 100%;
    max-height: 70vh;
    margin: 30px 0;
    border-radius: 15px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
}

#modal-image:hover {
