/* ÂìçÂ∫îÂºèËÆæËÆ° */
@media (max-width: 768px) {
    .container {
        margin: 10px;
        padding: 20px;
        border-radius: 20px;
    }

    header h1 {
        font-size: 28px;
    }

    section {
        padding: 25px;
    }

    section h2 {
        font-size: 24px;
    }

    .gift-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    .gift-media-container {
        height: 150px;
    }

    .letter-content {
        padding: 25px;
        font-size: 18px;
    }

    .music-player {
        padding: 15px 30px;
        gap: 15px;
    }

    #play-btn {
        padding: 10px 20px;
    }

    .save-tip {
        font-size: 16px;
        padding: 15px 20px;
    }

    .modal-content {
        margin: 10% auto;
        padding: 20px;
    }
}

@media (max-width: 480px) {
    .container {
        padding: 15px;
        border-radius: 15px;
    }

    header h1 {
        font-size: 24px;
    }

    section {
        padding: 20px;
    }

    section h2 {
        font-size: 20px;
    }

    .gift-grid {
        grid-template-columns: 1fr;
    }

    .gift-media-container {
        height: 150px;
    }

    .letter-content {
        padding: 20px;
        font-size: 16px;
    }

    .music-player {
        flex-direction: column;
        gap: 15px;
        padding: 20px;
        border-radius: 20px;
    }

    #volume {
        width: 100px;
    }

    .save-tip {
        font-size: 14px;
        padding: 12px 15px;
    }

    .modal-content {
        margin: 15% auto;
        padding: 15px;
    }

    .modal-nav {
        flex-direction: column;
    }

    .modal-nav button {
        padding: 10px 20px;
    }
}